import * as React from 'react';
import Layout from '../layouts/main';
import { NextPage } from 'next';
import { AntdFormMiddlewareProps } from '../src/share';
import { Button, message } from 'antd';

import DemoForm from '../components/DemoForm';

const schema = {
  title: 'A registration form',
  description: 'A simple form example.',
  type: 'object',
  properties: {
    username: { type: 'string' },
    firstname: { type: 'string' },
    lastname: { type: 'string' },
    email: { type: 'string' },
  },
};

const extraProps = {
  row: { justify: 'space-between' },
  properties: {
    firstname: { col: { span: 11 } },
    lastname: { col: { span: 11 } },
  },
};

const defaultData = {
  username: 'jhon',
  firstname: 'Jhon',
  lastname: 'Smith',
};

const MyButtonsMw: React.FC<AntdFormMiddlewareProps> = (props) => {
  const {
    parent,
    next,
    onChange,
    formProps: { defaultData },
  } = props;

  // if not on the root node, pass the control to the next middleware
  if (parent) return next(props);

  const [submitting, setSubmitting] = React.useState(false);
  const handleSubmit = async () => {
    setSubmitting(true);
    await require('bluebird').delay(1000);
    // pactically, should check unmounted here
    setSubmitting(false);
    message.success('Submited!');
  };
  const handleClear = () => onChange({});
  const handleReset = () => onChange(defaultData);

  return (
    <>
      {next(props) /* render the body of the form */}
      <Button type="primary" onClick={handleSubmit} loading={submitting}>
        Submit
      </Button>{' '}
      <Button onClick={handleClear}>Clear</Button> <Button onClick={handleReset}>Reset</Button>
    </>
  );
};

const code = `
const middlewares = [
  MyButtonsMw,
  ExtraPropsMw,
  ColMw,
  FieldsetTemplateMw,
  RowMw,
  FormItemTemplateMw,
  ...schemaMws,
  InputMw,
  NotSupportedMw,
];

render(
  <Form
    defaultData={defaultData}
    layout="vertical"
    schema={schema}
    extraProps={extraProps}
    middlewares={middlewares}
  />
);
`;

const IndexPage: NextPage = () => {
  return (
    <Layout>
      <DemoForm code={code} scope={{ schema, extraProps, defaultData, MyButtonsMw }} />
    </Layout>
  );
};

export default IndexPage;
